import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';

import { Products } from '../_models/index';

@Injectable()
export class ProductsService {

    constructor(private http: Http) { }

    getAll() {
        return this.http.get('/restaurant').map((response: Response) => response.json());
    }

    getById(_id: string) {
        console.log("getById done : "+_id);
        return this.http.get('/restaurant/current/' + _id).map((response: Response) => response.json());
    }

    getByRestaurantId(restaurantId: string) {
        return this.http.post('/restaurant/getproducts/',{restaurantId : restaurantId}).map((response: Response) => response.json());
    }

    create(Products: Products) {
        return this.http.post('/restaurant/products', Products).map((response: Response) => response);
    }

    update(Products: Products) {
        return this.http.put('/restaurant/' + Products._id, Products).map((response: Response) => response);
    }

    // delete(Products: Products){ //_id: string) {
    //     //return this.http.delete('/Products/' + _id).map((response: Response) => response.json());
    //     // this is soft delete data
    //     return this.http.put('/Products/' + Products._id, Products).map((response: Response) => response);
    //     // this is hard delete to db data
    //     //return this.http.delete('/employees/' + _id);
    // }

}



