import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Order } from '../_models/index';

@Injectable()
export class OrderService {

  constructor(private http: Http) { }

  getAll() {
      return this.http.get('/order').map((response: Response) => response.json());
  }

  getById(_id: string) {
      return this.http.get('/orderdetails/current/' + _id).map((response: Response) => response.json());
  }

  getByRestaurantId(restaurantId: string) {
      return this.http.post('/orderdetails/getorders/',{restaurantId : restaurantId}).map((response: Response) => response.json());
  }

  create(order: Order) {
      return this.http.post('/orderdetails/register', order).map((response: Response) => response);
  }

  update(order: Order) {
      return this.http.put('/orderdetails/' + order._id, order).map((response: Response) => response);
  }

}



