import { Component, OnInit, Input, Output,EventEmitter } from '@angular/core';
import { OrderService , AlertService, AuthenticationService } from '../../_services/index';
import { User } from '../../_models/user';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-orderdetails',
  templateUrl: './orderdetails.component.html',
  styleUrls: ['./orderdetails.component.css']
})
export class OrderdetailsComponent implements OnInit {

  order : any = {};
  orders : any;
  settings : any;
  datas : any;
  user = new User();
  _id : string;
  data:Array<any>;
  display='none'; /** to show or hide modal popup window */


  constructor(private orderService :  OrderService, private alertService : AlertService) { 
    this.user = JSON.parse(localStorage.getItem('currentUser'));

        this.settings = {
          actions: {
                      custom: [
                        {
                          name: 'view',
                          title: '<a class="btn btn-default" href="#" aria-label="Delete"> <i class="fa fa-eye" aria-hidden="true"></i></a>',
                        },
                        {
                          name: 'print',
                          title: '&nbsp;<a class="btn btn-default" href="#" aria-label="Delete"> <i class="fa fa-print" aria-hidden="true"></i></a>',
                        },
                      ]
          },
          add: '',
          edit: '',
          delete: {
            confirmDelete: true,
          },
          // add: {
          //   confirmCreate: true,
          // },
          // edit: {
          //   confirmSave: true,
          // },
          columns: {
            employeeId: {
              title: 'Employee Id'
            },
            orderId: {
              title: 'Order Number'
            },
            tableNumber: {
              title: 'Table Number'
            },
            createdDate: {
              title: 'Date',
              valuePrepareFunction: (date) => {
                    var raw = new Date(date);
                    var formatted = new DatePipe('en-EN').transform(raw, 'dd MMM yyyy hh:mm:ss a');
                    return formatted;
                }            
            },
            noOfCust: {
              title : 'No of Customer'
            },
            orderStatus: {
              title : 'Order Status'
            }           
          }
        };

        
        this.loadOrderDetails();
  }

  onViewOrPrint(event) {
    // alert(`Custom event '${event.action}' fired on row : ${event.data}`)
    if(event.action == 'view'){
      this.display='block'; 
      this.order = event.data;
      console.log(event)
    }else if(event.action == 'print'){
      console.log('print click');
    }

  }
  onCloseHandled(){
      this.display='none'; 
  }

  onPrint(){
    
  }

  ngOnInit() {
  }


  /**
   * Load all orders details to grid
   */
  loadOrderDetails(){
    console.log("restaurant loadOrderDetails start!");
    this.orderService.getByRestaurantId(this.user.userId).subscribe(data => { 
      console.log(data);
      this._id = data._id;
      this.data = data.orderDetails; 
    },error => {
      this.alertService.error(error);
    });
  }

  /**
   * 
   * @param event Soft delete the order details
   */
  onDeleteConfirm(event) {
    if (window.confirm('Are you sure you want to delete?')) {
      event.data._id = this._id;
      event.data.modifiedBy = this.user.username;
      event.data.deleteFlag = true;
      // console.log("deleteorder  : "+JSON.stringify(event.data));
      this.orderService.update(event.data).subscribe(() => { 
        this.loadOrderDetails(); 
      },error => {
        this.alertService.error(error);
      });
      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }

  /**
   * 
   * @param event Update selected order details
   */
  onSaveConfirm(event) {
    event.newData._id = this._id;
    event.newData.modifiedBy = this.user.username;
    console.log(event.newData);
    if (window.confirm('Are you sure you want to save?')) {
      //event.newData['name'] += ' + added in code';
        event.newData.deleteFlag = false;
        this.orderService.update(event.newData).subscribe(datas => { 
          this.loadOrderDetails();
        },error => {
          this.alertService.error(error);
        });
      event.confirm.resolve(event.newData);
    } else {
      event.confirm.reject();
    }
  }

  /**
   * 
   * @param event add new order to grid as wel as database
   */
  onCreateConfirm(event) {
    if (window.confirm('Are you sure you want to create?')) {
      // event.newData['name'] += ' + added in code';
      // console.log(this.user);
      event.newData.createdBy = this.user.username;
      event.newData.restaurantId = this.user.userId;
      console.log(event.newData);
      this.orderService.create(event.newData).subscribe(datas => { 
        // this.orders = data; 
        this.loadOrderDetails();
      },error => {
        this.alertService.error(error);
      });
      event.confirm.resolve(event.newData);
    } else {
      event.confirm.reject();
    }
  }

}



