import { Component, OnInit, OnDestroy } from '@angular/core';
import { ChatService } from '../../_services/index';
import * as $ from 'jquery';

@Component({
  selector: 'app-kichen',
  templateUrl: './kichen.component.html',
  styleUrls: ['./kichen.component.css']
})
export class KichenComponent implements OnInit, OnDestroy {

  connection;
  messages = [];
  message : string;
  constructor(private chatService :  ChatService) { 
    this.message = 'Delivered';
  }

  sendDeliveredStatus(event,id,message){
    console.log("event :: "+id + " : "+message)
    document.getElementById(id).style.display = "none"
    //this.chatService.sendMessage(message);
  }
  ngOnInit() {
      this.connection = this.chatService.getMessages().subscribe(message => {
        this.messages.push(message);
      })
  }

  ngOnDestroy(){
    this.connection.unsubscribe();
  }

}



