import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name : 'deleteCategory'
})
export class DeleteFlagPipe implements PipeTransform{
    transform(items : Array<any>, deleteFlag : boolean ): Array<any>{
        return items.filter(item => item.deleteFlag != deleteFlag);
    }
}


