import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { ChartsModule } from 'ng2-charts';
import { Ng2SmartTableModule } from 'ng2-smart-table';

import { routing } from './app.routing';
import { customHttpProvider } from './_helpers/index';
import { AlertComponent, OnlyNumber } from './_directives/index';
import { AuthGuard } from './_guards/index';
import { AlertService, AuthenticationService, UserService, 
         EmployeeService, ProductsService, OrderService, ChatService } from './_services/index';
import { DeleteFlagPipe } from './_pipes/deleteFlagPipe';

import { AppComponent } from './app.component';
import { HomeComponent } from './pages/home/home.component';
import { LoginComponent } from './pages/login/login.component';
import { RegisterComponent } from './pages/register/register.component';
import { MainmenuComponent } from './pages/mainmenu/mainmenu.component';
import { HeaderComponent } from './pages/header/header.component';
import { BaseChartComponent } from './pages/base-chart/base-chart.component';
import { EmployeeRegisterComponent } from './pages/employee-register/employee-register.component';
import { FoodProductsComponent } from './pages/food-products/food-products.component';
import { DineComponent } from './pages/dine/dine.component';
import { OrderdetailsComponent } from './pages/orderdetails/orderdetails.component';
import { KichenComponent } from './pages/kichen/kichen.component';

/** Pipe */
import { DateFormatPipe, DateTimeFormatPipe } from './_pipes/dateformatpipe';


@NgModule({
  declarations: [
    AppComponent,
    AlertComponent,
    HomeComponent,
    LoginComponent,
    RegisterComponent,
    MainmenuComponent,
    HeaderComponent,
    BaseChartComponent,
    EmployeeRegisterComponent,
    FoodProductsComponent,
    DeleteFlagPipe,
    OnlyNumber,
    DineComponent,
    OrderdetailsComponent,
    KichenComponent,
    DateFormatPipe,
    DateTimeFormatPipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    routing,
    ChartsModule,
    Ng2SmartTableModule
  ],
  providers: [
    customHttpProvider,
    AuthGuard,
    AlertService,
    AuthenticationService,
    UserService,
    EmployeeService,
    ProductsService,
    OrderService,
    ChatService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



