import { Component, OnInit } from '@angular/core';
import { UserService , AlertService, AuthenticationService } from '../../_services/index';
import { Router } from '@angular/router';
import { Restaurant } from '../../_models/restaurant';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  userModel : any = {};
  loginModel : any = {};
  returnData : any = {};
  data : any;
  restaurant = new Restaurant();

  constructor(private userService :  UserService, private alertService : AlertService, 
              private authService : AuthenticationService, private router: Router) { }


  ngOnInit() {
  }

  registerRestaurant(){
    // console.log("restaurant registered start!");
    this.userService.create(this.userModel).subscribe(data => { 
      console.log(data.ops[0]);
      this.returnData = data; 
      this.restaurant.restaurantName = this.returnData.ops[0].restaurantName;
      this.restaurant.createdBy = this.returnData.ops[0].userName;
      console.log(this.restaurant);
      this.alertService.success("Successfully created");
      this.userService.createRestaurant(this.restaurant).subscribe(datas => { 
        console.log(datas);
        this.router.navigate(['/login']);
          },error => {
        this.alertService.error(error);
      });
      // console.log("restaurant registered successfully!");
    },error => {
      this.alertService.error(error);
    });
    // console.log("restaurant registered end!");
  }

  signin(){
       this.router.navigate(['/login']);
  }
}



