import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../../_models/index';
import { AlertService, AuthenticationService } from '../../_services/index';

@Component({
  selector: 'header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  currentUser: User;
  restaurantName : string = 'Dine'
  constructor(private router: Router,private authService : AuthenticationService) {
     if(localStorage.getItem('currentUser')){
        this.currentUser = JSON.parse(localStorage.getItem('currentUser'))
        // console.log("this.user :; "+this.currentUser);
        this.restaurantName = this.currentUser.restaurantName;
     }
   }

  ngOnInit() {


  }


  loginPage(){
     this.router.navigate(['/login']);
  }

  logout(){
    this.authService.logout();
  }

  empSignup(){
    this.router.navigate(['/empsignup']);
  }

  productsConfig(){
    this.router.navigate(['/products']);
  }

  dashBoard(){
    this.router.navigate(['/dashboard']);
  }

  tableConfig(){
    this.router.navigate(['/tableconfig']);
  }

  navigateToOrderPage(){
    this.router.navigate(['/orders']);
  }
  
  navigateToKichenPage(){
    this.router.navigate(['/kichen']);
  }

  goToMainMenu(){
    this.router.navigate(['/mainmenu']);
  }
  
}



