import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';

import { Employee } from '../_models/index';

@Injectable()
export class EmployeeService {

    constructor(private http: Http) { }

    getAll() {
        return this.http.get('/employees').map((response: Response) => response.json());
    }

    getById(_id: string) {
        return this.http.get('/employees/current/' + _id).map((response: Response) => response.json());
    }

    getByRestaurantId(restaurantId: string) {
        return this.http.post('/employees/getemployees/',{restaurantId : restaurantId}).map((response: Response) => response.json());
    }

    create(employee: Employee) {
        return this.http.post('/employees/register', employee).map((response: Response) => response);
    }

    update(employee: Employee) {
        return this.http.put('/employees/' + employee._id, employee).map((response: Response) => response);;
    }

    // delete(employee: Employee){ //_id: string) {
    //     // this is soft delete data
    //     return this.http.put('/employees/' + employee._id, employee).map((response: Response) => response);;
    //     // this is hard delete to db data
    //     //return this.http.delete('/employees/' + _id);
    // }

}



