import { Component, OnInit } from '@angular/core';
import { ProductsService , AlertService, AuthenticationService } from '../../_services/index';
import { User } from '../../_models/user';
import { Router } from '@angular/router';

@Component({
  selector: 'app-food-products',
  templateUrl: './food-products.component.html',
  styleUrls: ['./food-products.component.css']
})
export class FoodProductsComponent implements OnInit {

  product : any = {};
  products : any;
  settings : any;
  datas : any;
  user = new User();
  _id : string;

  data:Array<any>; // = TableData; // table json data

  private foodSessionList = [{name:'BreakFast',value:'BreakFast'},
							  {name:'Lunch',value:'Lunch'},
							  {name:'Snacks',value:'Snacks'},
							  {name:'Dinner',value:'Dinner'}];

  constructor(private productsService :  ProductsService, private alertService : AlertService, 
              private authService : AuthenticationService, private router: Router) { 
    this.user = JSON.parse(localStorage.getItem('currentUser'));

    this.settings = {
      delete: {
        confirmDelete: true,
      },
      add: {
        confirmCreate: true,
      },
      edit: {
        confirmSave: true,
      },
      columns: {
        productName: {
          title: 'Product Name'
        },
        category: {
          title: 'Category'
        },
        subCategory: {
          title: 'Sub Category'
        },
        price: {
          title: 'Price'
        },
        foodSession: {
          title : 'Food Session'
        }
      }
    };
     this.loadProducts();
  }


  ngOnInit() {
     
  }

  /**
   * Load all products details to grid
   */
  loadProducts(){
    console.log("restaurant loadEmployeeDetails start!");
    this.productsService.getByRestaurantId(this.user.userId).subscribe(data => { 
      console.log(data);
      this._id = data._id;
      this.data = data.productDetails; 
    },error => {
      this.alertService.error(error);
    });
  }

  /**
   * 
   * @param event Soft delete the product details
   */
  onDeleteConfirm(event) {
    if (window.confirm('Are you sure you want to delete?')) {
      event.data._id = this._id;
      event.data.modifiedBy = this.user.username;
      event.data.deleteFlag = true;
      // console.log("deleteProduct  : "+JSON.stringify(event.data));
      this.productsService.update(event.data).subscribe(() => { 
        this.loadProducts(); 
      },error => {
        this.alertService.error(error);
      });
      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }

  /**
   * 
   * @param event Update selected product details
   */
  onSaveConfirm(event) {
    event.newData._id = this._id;
    event.newData.modifiedBy = this.user.username;
    console.log(event.newData);
    if (window.confirm('Are you sure you want to save?')) {
      //event.newData['name'] += ' + added in code';
        event.newData.deleteFlag = false;
        this.productsService.update(event.newData).subscribe(datas => { 
          this.loadProducts();
        },error => {
          this.alertService.error(error);
        });
      event.confirm.resolve(event.newData);
    } else {
      event.confirm.reject();
    }
  }

  /**
   * 
   * @param event add new product to grid as wel as database
   */
  onCreateConfirm(event) {
    if (window.confirm('Are you sure you want to create?')) {
      // event.newData['name'] += ' + added in code';
      // console.log(this.user);
      event.newData.createdBy = this.user.username;
      event.newData.restaurantId = this.user.userId;
      console.log(event.newData);
      this.productsService.create(event.newData).subscribe(datas => { 
        // this.products = data; 
        this.loadProducts();
      },error => {
        this.alertService.error(error);
      });
      event.confirm.resolve(event.newData);
    } else {
      event.confirm.reject();
    }
  }

}



