import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './pages/home/index';
import { LoginComponent } from './pages/login/index';
import { RegisterComponent } from './pages/register/index';
import { MainmenuComponent } from './pages/mainmenu/mainmenu.component';
import { EmployeeRegisterComponent } from './pages/employee-register/employee-register.component';
import { FoodProductsComponent } from './pages/food-products/food-products.component';
import { BaseChartComponent } from './pages/base-chart/base-chart.component';
import { DineComponent } from './pages/dine/dine.component';
import { OrderdetailsComponent } from './pages/orderdetails/orderdetails.component';
import { KichenComponent } from './pages/kichen/kichen.component';
import { AuthGuard } from './_guards/index';

const appRoutes: Routes = [
    { path: '', component: HomeComponent}, //, canActivate: [AuthGuard] },
    { path: 'login', component: LoginComponent },
    { path: 'register', component: RegisterComponent },
    { path: 'mainmenu', component: MainmenuComponent },
    { path: 'empsignup', component: EmployeeRegisterComponent },
    { path: 'products', component: FoodProductsComponent },
    { path: 'dashboard', component: BaseChartComponent },
    { path: 'tableconfig', component: DineComponent },
    { path: 'orders', component: OrderdetailsComponent },
    { path: 'kichen', component: KichenComponent },
    

    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes);


